@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

html {
  scrollbar-width: none;
  scroll-behavior: smooth !important;
}

::-webkit-scrollbar {
  width: 0;
}

div#main {
  flex-direction: column;
  width: 100vw;
  display: flex;
}

section {
  width: 100vw;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 3rem 0;
}

section img.background {
  object-fit: cover;
  pointer-events: none;
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  text-shadow: 1px 1px 5px #00000026;
  margin: 0;
  font-family: Open Sans, sans-serif;
  font-size: 4.5rem;
  font-weight: 600;
  line-height: .8;
}

h2 {
  text-shadow: 1px 1px 5px #0000001a;
  margin: 0;
  padding-top: 1rem;
  font-size: 2.5rem;
  font-weight: 500;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-top: 0;
  margin-bottom: .8rem;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #038;
}

.container {
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 15px auto;
  display: grid;
}

.box {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 1rem;
}

.box:hover {
  border: 1px solid #038;
}

img.socialIcon {
  width: 30px;
  height: 30px;
}
